<template>
    <div class="container">
        <mt-swipe :auto="4000">
            <mt-swipe-item v-for="item in swiper" :key='item.id'>
                <a :href="item.icon">
                    <img :src="item.icon"/>
                </a>
                </mt-swipe-item>
        </mt-swipe>
        <div class="biglistbox">
          <div class="list">
              <div class="item" @click="el({icon,rou})" v-for="icon in firsticon" :key="icon.path">
                  <i :class="jsicon(icon)"></i>
                  <span class="title">{{icon.tit}}</span>
              </div>
          </div>
          <div class="list">
              <div class="item" @click="lasthomelink(item)" v-for="item in lasticon" :key="item.path">
                  <i :class="jsitem(item)"></i>
                  <span class="title">{{item.tit}}</span>
              </div>
          </div>
        </div>
        <div class="pro">——————<span>精选商品</span>——————</div>
        <div class="adv"><img src="../../static/home/img.jpg"></div>
        <div class="carousel">
            <ul>
                <li v-for="item in pro" :key="item.id"><a href="#"><img :src="item.img"></a></li>
            </ul>
        </div>
        <div class="product">
            <div>
                <div></div>
                <div></div>
            </div>
        </div>
        </div>
</template>

<script>
import { getSwiper, getPro } from '@/service'
import routes from '@/router/routes'
import { mapMutations } from 'vuex'
export default {
  name: 'home',
  data () {
    return {
      swiper: [],
      pro: []

    }
  },
  mounted () {
    getSwiper().then(resp => {
      this.swiper = resp.data.data
    })
    getPro().then(resp => {
      this.pro = resp.data.data
    })
  },
  computed: {
    firsticon () {
      return routes.filter(item => item.ishomelink === true)
    },
    lasticon () {
      return routes.filter(item => item.ishomelink === false)
    },
    jsicon () {
      return icon => `iconfont icon-${icon.icons}`
    },
    jsitem () {
      return item => `iconfont icon-${item.icons}`
    },
    rou () {
      return this.$router
    }
  },
  methods: {
    ...mapMutations(['el']),
    lasthomelink (item) {
      this.$router.history.push(item.path)
    }
  }
}
</script>

<style lang="scss">
.mint-swipe {
  width: 100%;
  height: 0;
  position: relative;
  padding-top: 50%;
  background: #abcdef;
  &-items-wrap {
    position: absolute;
    right: 0;
    top: 0;
    left: 0;
    bottom: 0;
    overflow: hidden;
    img {
      width: 100%;
    }
  }
}
.biglistbox{
  .list {
    width: 100%;
    display: flex;
    height: 64px;
    padding-top:10px;
    background-color: #fff;
    font-size:13px;
    .item {
      display: flex;
      flex: 1;
      flex-direction: column;
      text-align: center;
      color: #666;
      .iconfont {
        font-size: 30px;
        line-height: 40px;
      }
    }
  }
}
    .pro{
        display: flex;
        justify-content: center;
        align-items: center;
        padding:20px 0;
        color:#aaa;
        span{
            font-size:13px;
            color:black;
        }
    }
    .adv{
        height:80px;
        margin:5px 0;
        background: #666;
        overflow: hidden;
        img{
            width: 100%;
        }
    }
    .carousel{
        background: darkgray;
        overflow-y: hidden;
        display: relative;
        ul{
            display: flex;
            width: 225%;
            justify-content: space-between;
            li{
                width:50%;
                display: flex;
                justify-content: center;
                a{
                    img{
                        width: 100%;
                    }
                }
            }
        }
    }
</style>



